import firebase from 'firebase/compat/app';

export default {
  state: {
    info: {},
  },
  mutations: {
    SET_INFO(state, info) {
      state.info = info;
    },
    CLEAR_INFO(state) {
      state.info = {};
    },
  },
  actions: {
    async FETCH_INFO({ commit, dispatch }) {
      try {
        const uid = await dispatch('GET_ID');
        const info = (await firebase.database().ref(`/users/${uid}/info`).once('value')).val();
        commit('SET_INFO', info);
      } catch (e) {
        console.log(e);
      }
    },
  },
  getters: {
    INFO: (s) => s.info,
  },
};
